"use client";

import { useState } from "react";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Calendar } from "@/components/ui/calendar";
import { Plus, Calendar as CalendarIcon, Mic } from "lucide-react";
import { format } from "date-fns";
import { nb } from "date-fns/locale";

export function NewMeetingDialog() {
  const [date, setDate] = useState<Date | undefined>(new Date());
  const [time, setTime] = useState<string>("");
  const [title, setTitle] = useState<string>("");
  const [meetingType, setMeetingType] = useState<"live" | "scheduled">("live");

  return (
    <Dialog>
      <DialogTrigger asChild>
        <Button className="bg-violet-600 hover:bg-violet-700 gap-2 px-8">
          <Plus className="h-4 w-4" />
          Nytt møte
        </Button>
      </DialogTrigger>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle>Opprett nytt møte</DialogTitle>
        </DialogHeader>
        <div className="grid gap-4 py-4">
          <div className="grid gap-2">
            <Label htmlFor="title">Møtetittel</Label>
            <Input
              id="title"
              placeholder="Skriv inn møtetittel"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
            />
          </div>

          <div className="grid gap-2">
            <Label>Møtetype</Label>
            <div className="flex gap-2">
              <Button
                variant={meetingType === "live" ? "default" : "outline"}
                className="flex-1"
                onClick={() => setMeetingType("live")}
              >
                <Mic className="mr-2 h-4 w-4" />
                Live møte
              </Button>
              <Button
                variant={meetingType === "scheduled" ? "default" : "outline"}
                className="flex-1"
                onClick={() => setMeetingType("scheduled")}
              >
                <CalendarIcon className="mr-2 h-4 w-4" />
                Planlagt møte
              </Button>
            </div>
          </div>

          {meetingType === "scheduled" && (
            <>
              <div className="grid gap-2">
                <Label>Dato</Label>
                <Calendar
                  mode="single"
                  selected={date}
                  onSelect={setDate}
                  locale={nb}
                  className="rounded-md border"
                />
              </div>
              <div className="grid gap-2">
                <Label htmlFor="time">Tid</Label>
                <Input
                  id="time"
                  type="time"
                  value={time}
                  onChange={(e) => setTime(e.target.value)}
                />
              </div>
            </>
          )}
        </div>
        <div className="flex justify-end gap-2">
          <Button variant="outline">Avbryt</Button>
          <Button className="bg-violet-600 hover:bg-violet-700">
            {meetingType === "live" ? "Start live møte" : "Planlegg møte"}
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
}
